@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap");

body {
    font-size: 14px;
    line-height: 1.5em;
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    margin: 0;
}

h1 {
    font-size: 2rem;
    line-height: 1.5em;
margin-top: 0;
}
h2 {
    font-size: 1.7rem;
    line-height: 1.5em;

}
h3 {
    font-size: 1.4rem;
    line-height: 1.5em;

}
p {
    font-size: 1rem;
    line-height: 1.5em;

}
a {
    text-decoration: none;
}
.skip-link {
    display: none;
}
.container {
    max-width: 992px;
    padding: 0 20px;
    margin: 0 auto;
    width: 100%;
    box-sizing: content-box;
}
.display-flex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}

#header {
    background-color: #c2a5d1;
    padding: 10px;
    position: sticky;
}

nav {
    ul {
        list-style: none;
    }
    a {
        color: #fff;
    }
}
.logo--link {
    font-size: 2rem;
    letter-spacing: 2px;
    color: #fff;
}
main {
    display: flex;
    justify-content: center;
    min-height: calc(100vh - 193px);
}

#footer {
    text-align: center;
    padding: 50px 20px;
    background-color: #000;
    color: #fff;
    text-align: center;
    p {
        margin: 0;
    }
}

#check-availabilities {
    box-sizing: border-box;
    max-width: 650px;
    margin: 50px auto;
    padding: 40px 30px;
    border-radius: 30px;
    -webkit-border-radius: 30px;
    box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: 0 0 15px 0px rgba(0, 0, 0, 0.1);
}
.check-availabilities--btn--wrapper{
    display: flex;
    justify-content: center;
}
.btn {
    font-size: 1.3rem;
    cursor: pointer;
    color: #fff;
    background-color: #000;
    padding: 0 40px;
    margin: 20px auto 0;
    height: 60px;
    line-height: 50px;
    text-align: center;
    border-radius: 30px;
    -webkit-border-radius: 30px;
}

.day--btn{
        display: flex;
        flex-direction: column;
        border-radius: 50%;
}

.apply-button {

}

.room-card{
    display: flex;

}